import React, { useState, useEffect } from "react";
import Loader from "react-loader-spinner";

import PropTypes from "prop-types";

const Spinner = props => {
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    setIsLoading(props.isLoading);
  }, [props.isLoading]);

  return isLoading ? (
    <div
      style={{
        position: "absolute",
        // width: 150,
        // height: 150,
        borderRadius: 20,
        left: "50%",
        top: "50%",
        marginLeft: "-50px",
        marginTop: "-50px",
        zIndex: 10,
        background: "black",
        opacity: 0.4
      }}
    >
      <div style={{ padding: 35 }}>
        <Loader
          width={100}
          height={100}
          type="Bars"
          visible={true}
          color="#00BFFF"
        />
      </div>
    </div>
  ) : null;
};

Spinner.prototype = {
  isLoading: PropTypes.bool
};

export { Spinner };
